*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background:#3e65fe ;
}
.logo-box{
  background: white;
  padding: 30px 120px;
  width: 550px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  text-align: center;
}
.logo{
  width: 30px;
  margin-bottom: 20px;
}
.logo-box h2{
  margin-bottom: 20px;
}
.logo-box button{
  width: 100%;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0px;
  border-radius: 30px;
  background: transparent;
  border: 1px solid #999;
  font-weight: 500;
  cursor: pointer;
}
.logo-box button img{
  width: 18px;
  margin-right: 10px;
}
.logo-box span {
  display: block;
  width: 40px;
  margin: auto;
  background: #fff;
  font-size: 14px;
  margin-top: -10px;
}
.logo-box input{
  width: 100%;
  background: transparent;
  border: 1px solid #999;
  outline: 0;
  padding: 15px 10px;
  border-radius: 4px;
}
::placeholder{
  font-weight: 500;
  color: #898484;
}
.logo-box form button{
  background: black;
  color: white;
  border: 0;
  margin-bottom: 15px;
}
.logo-box p{
  font-size: 15px;
  color: #555;
  margin: 14px 0px 50px;
}
.logo-box p a{
  text-decoration: none;
  color: #359cf0;
  margin-left: 90px;
}